.hero{
  position: relative;
  margin-top: -60px;
  min-height: 100vh;
  background-image: url("/img/back-hero.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  overflow: hidden;
  &__i{
    position: absolute;
    z-index: 50;
    width: 100%;
    height: 100%;
    bottom: 0;
    background-image: url("/img/hero.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }
  &__title{
    position: relative;
    margin: 0;
    margin-top: auto;
    position: relative;
    overflow: hidden;
    font-weight: 500;
    font-size: 150px;
    color: var(--off-white);
    white-space: nowrap;
    flex-shrink: 0;
    padding: 0 50px;
    animation: ticker 17s linear infinite;
    &::before{
      position: absolute;
      content: "";
      height: 40px;
      width: 100%;
      font-size: 15;
    }
    @include small-tablet{
      font-size: 90px;
    }
  }
  &__hello{
    display: none;
    position: absolute;
    overflow: hidden;
    top: 50%;
    right: -370px;
    background-color: var(--black);
    border-radius: 64px 0 0 64px;
    font-weight: 400;
    font-size: 40px;
    color: #fff;
    padding: 25px;
    padding-left: 90px;
    transition: right 0.8s ease-in-out;
    transform: scale(1);
    &::before{
      content: "";
      position: absolute;
      left: 20px;
      top: 50;
      width: 56px;
      height: 56px;
      background-image: url(../img/hello.png);
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      animation: hello 5s linear infinite;
    }
    &:hover{
      right: 0;
    }
    @keyframes hello {
      0% {
        transform: rotate(0deg);
      }
      50%{
        transform: rotate(20deg);
      }
      100% {
        transform: rotate(0deg);
      }
    }
  }
}
@keyframes ticker {
  0%{
    transform: translateZ(0);
  }
  to{
    transform: translate3d(-100%, 0, 0);
  }
}
