.cover{
  margin-top: 160px;
  @include tablet {
    margin-top: 80px;
  }
  @include mobile {
    margin-top: 60px;
  }
  &__top{
    display: flex;
    margin-bottom: 40px;
  }
  &__bottom{
    display: flex;
  }
  &__item{
    position: relative;
    border-radius: 4px;
    padding: 56px 48px;
    background-color: #f0f8ff;
  }
  &__link{
    position: absolute;
    top: 42%;
    left: 35%;
    border-radius: 4px;
    padding: 12px 15px 12px 16px;
    background-color: #fff;
    font-weight: 500;
    font-size: 16px;
    color: var(--black);
    opacity: 0;
    transition: opacity 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19);
    &-svg{
     margin-left: 10px;
    }
  }

}
.swiper-slide{
  width: 444px;
  height: 306px;
  @include mobile {
    width: 315px;
    height: 260px;
  }
}

.cover__item:hover + .cover__link{
  opacity: 1;
}

.cover__link:hover {
  opacity: 1;
}
