.works{
  margin-top: 90px;
  &__container{
    @include mobile{
      display: flex;
      flex-direction: column;
    }
  }

  &__title{
    margin: 0;
    grid-column: 3 span;
    font-weight: 600;
    font-size: 40px;
    color: var(--black);
    @include tablet{
      grid-column: 4 span;
    }
    @include small-tablet{
      font-size: 30px;
    }
    @include mobile{
      margin-bottom: 40px;
    }
  }
  &__list{
    grid-column: 5 / 8 span;
  }
  &__item{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 32px;
    border-bottom: 1px solid var(--light-greay);
    &-title{
      margin: 0;
      font-family: var(--second-family) ;
      font-weight: 400;
      font-size: 60px;
      color: var(--black);
      @include small-tablet{
        font-size: 45px;
      }
      @include small-mobile{
        font-size: 35px;
      }

    }
    &-subtitle{
      font-weight: 400;
      font-size: 18px;
      line-height: 160%;
      color: var(--dark-greay);
    }
    &-btn{
      border: 1px solid var(--dark-greay);
      border-radius: 180px;
      padding: 20px;
      width: 85px;
      height: 85px;
      transition: background-color 0.5s ease-in-out, scale 0.5s ease-in-out;
      &:hover{
        background-color: var(--dark-greay);
        scale: (1.1);
      }
      &:hover .svg{
        fill: #F4F7FA;
      }
      @include small-tablet{
        padding: 14px;
        width: 75px;
        height: 75px;
      }
    }
  }
}

