.header{
  position: relative;
  z-index: 1000;
  &__container{
    padding-top: 30px;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    height: 30px;
    display: inline-block;
    overflow: hidden;
    font-size: 18px;
    line-height: 29px;
    color: var(--off-white);
    overflow: hidden;
    margin: 0;
  }
}
.nav{
  @include mobile{
    // display: none;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    width: 80%;
    height: 100vh;
    font-size: 40px;
    background-color: var(--black);
    padding: 32px;
    overflow-y: auto;
    visibility: hidden;
    transform: translateY(-100%);
    transition: transform 0.5s cubic-bezier(0.895, 0.03, 0.685, 0.22), visibility 0.5s cubic-bezier(0.895, 0.03, 0.685, 0.22),  ;
  }
  &__list{
    display: flex;
    gap: 40px;
    @include mobile{
       flex-direction: column;
       align-items: flex-start ;
    }

  }
  &__link{
    position: relative;
    font-size: 18px;
    color: var(--off-white);
    transition: line-height 1s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    &:hover{
      line-height: 10px;
    }
    &::before{
      content: "";
      position: absolute;
      bottom: -19px;
      left: 50%;
      width: 12px;
      height: 12px;
      border-radius: 50%;
      background-color: aliceblue;
      scale: (0);
      transition: scale 0.7s cubic-bezier(0.075, 0.82, 0.165, 1);
    }
    &:hover::before{
      scale: (1);
    }

  }
}
.nav.menu--active{
  visibility: visible;
  transform: none;
}
