.about{
  margin-top: 80px;
  &__container{
    @include mobile{
      display: flex;
      flex-direction: column;
    }
  }
  &__title{
    grid-column: 2 span;
    font-weight: 600;
    font-size: 40px;
    color: var(--black);

    @include tablet{
      grid-column: 3 span;
    }
    @include small-tablet{
      font-size: 30px;
    }
    @include mobile{
      grid-column: 4 span;
    }
  }
  &__descr{
    grid-column: 5 / 8 span;
    font-weight: 400;
    font-size: 25px;
    line-height: 140%;
    color: var(--dark-greay);
    @include tablet{
      font-size: 22px;
    }
    @include small-tablet{
      font-size: 18px;
    }
  }
}
