.info{
  background-color: #0B0C0E;
  padding: 80px 0;
  &__container{
    display: flex;
    justify-content: space-around;
    align-items: center;
    @include mobile{
      flex-direction: column;
      align-items: flex-start;
    }
  }
  &__right{
    @include mobile{
      margin-top: 45px;
    }
  }
  &__left{
    @include mobile{
      align-self: center;
    }
  }
  &__img{
    border-radius: 186px;
    width: 297px;
    height: 297px;

  }
  &__contact{
    margin-top: 60px;
    &-title{
      margin: 0;
      font-weight: 400;
      font-size: 18px;
      line-height: 160%;
      color: var(--off-white);
      margin-bottom: 8px;
    }
    &-mail{
      margin: 0;
      font-weight: 400;
      font-size: 27px;
      line-height: 140%;
      color: var(--off-white);
      @include small-mobile{
        font-size: 24px;
      }
    }
 }
 &__item{
  position: relative;
  font-weight: 400;
  font-size: 27px;
  line-height: 140%;
  color: transparent;
  -webkit-text-stroke: 0.2px var(--off-white);
  &-telegram{
    &::before{
      content: "Тelegram";
      position: absolute;
      width: 0;
      height: 100%;
      overflow: hidden;
      color: #24A1DE;
      transition: 1.5s cubic-bezier(0.075, 0.82, 0.165, 1);
    }
    &:hover::before{
      width: 100%;
      filter: drop-shadow(0 0 10px #24A1DE);
    }
  }
  &-instagram{
    &::before{
      content: "Instagram";
      position: absolute;
      width: 0;
      height: 100%;
      overflow: hidden;
      color: #de2491;
      transition: 1.5s cubic-bezier(0.075, 0.82, 0.165, 1);
    }
    &:hover::before{
      width: 100%;
      filter: drop-shadow(0 0 10px #de2491);
    }
  }
  &-vk{
    &::before{
      content: "VK";
      position: absolute;
      width: 0;
      height: 100%;
      overflow: hidden;
      color: #1635e6;
      transition: 1.5s cubic-bezier(0.075, 0.82, 0.165, 1);
    }
    &:hover::before{
      width: 100%;
      filter: drop-shadow(0 0 10px #1635e6);
    }
  }
  &-github{
    &::before{
      content: "GitHub";
      position: absolute;
      width: 0;
      height: 100%;
      overflow: hidden;
      color: #393a3f;
      transition: 1.5s cubic-bezier(0.075, 0.82, 0.165, 1);
    }
    &:hover::before{
      width: 100%;
      filter: drop-shadow(0 0 10px #393a3f);
    }
  }

 }
}
.info__links{
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  gap: 8px;
  &-title{
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    color: var(--off-white);
  }
}
